<template>
    <div class="Collectioncontent">
        <div class="canvas-content">
            <canvas ref="canvas" id="canvas" width="200" height="200"></canvas>
            <div class="btn-groups">
                <button>今日收藏</button>
                <button @click="goState('collectioncontenttest')">全部收藏</button>
            </div>
        </div>
    </div>
</template>

<style lang="less">
    .Collectioncontent {
        padding: 3rem 0;

        .canvas-content {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;

            .btn-groups {
                margin-top: 3rem;
                flex-basis: 100%;
                display: flex;
                justify-content: space-around;

                button {
                    cursor: pointer;
                    color: green;
                    font-weight: bold;
                    background: white;
                    border: none;
                    padding: 0.8rem 3rem;
                    border-radius: 2rem;

                    &:focus {
                        outline: none;
                    }
                }
            }
        }
    }
</style>

<script>
    let st1;
    export default {
        name: 'Collectioncontent',
        props:['clearnum'],
        methods: {
            goState(path) {
                this.$router.push(path)
            }
        },
        mounted() {
            let canvas = this.$refs.canvas
            let ctx = canvas.getContext('2d')
            ctx.arc(100, 100, 85, 0, 2 * Math.PI)
            ctx.fillStyle = '#ffffff29';
            ctx.fill()
            ctx.beginPath();
            ctx.arc(100, 100, 70, 0, 2 * Math.PI);
            ctx.fillStyle = '#ffffff';
            ctx.fill()
            ctx.beginPath();
            ctx.fillStyle = 'green'
            ctx.textAlign = 'center';
            ctx.font = '4.5rem sans-serif';
            ctx.fillText(this.CollectionNum.length, 100, 100, 200);
            ctx.beginPath()
            ctx.fillStyle = 'green'
            ctx.textAlign = 'center';
            ctx.font = '1.3rem sans-serif';
            ctx.fillText('我的收藏', 100, 130, 200);
            ctx.beginPath()
        },
        created() {
            console.log(this.CollectionNum)
            let Collection = Array.from(new Set(window.localStorage.CollectionQuestion.split(',')))
            if (Collection[0] == '') {
                Collection.splice(0, 1)
            }
            this.CollectionNum = Collection
            console.log(this.CollectionNum.length)
        },
        watch: {
            clearnum() {
                let canvas = this.$refs.canvas
                let ctx = canvas.getContext('2d')
                ctx.clearRect(0, 0, 200, 200);
                ctx.arc(100, 100, 85, 0, 2 * Math.PI)
                ctx.fillStyle = '#ffffff29';
                ctx.fill()
                ctx.beginPath();
                ctx.arc(100, 100, 70, 0, 2 * Math.PI);
                ctx.fillStyle = '#ffffff';
                ctx.fill()
                ctx.beginPath();
                ctx.fillStyle = 'green'
                ctx.textAlign = 'center';
                ctx.font = '4.5rem sans-serif';
                ctx.fillText('0', 100, 100, 200);
                ctx.beginPath()
                ctx.fillStyle = 'green'
                ctx.textAlign = 'center';
                ctx.font = '1.3rem sans-serif';
                ctx.fillText('我的收藏', 100, 130, 200);
                ctx.beginPath()
            }
        },
    }
</script>