<template>
    <div class="collectioncontent">
        <Collectionalert @Erroralerttoggle="Erroralerttogglefn" v-if="Erroralert.active"></Collectionalert>
        <div class="collectioncontent-header">
            <Collectiontitle></Collectiontitle>
            <CollectioncontentCanvas :clearnum="clearnum"></CollectioncontentCanvas>
        </div>
        <div class="collectioncontent-btn">
            <button @click="clearCollection">清空我的收藏</button>
        </div>
    </div>
</template>

<script>
    import CollectioncontentCanvas from './CollectioncontentCanvas'
    import Collectiontitle from './Collectiontitle'
    import Collectionalert from './Collectionalert'
    export default {
        name: 'collectioncontent',
        components: {
            CollectioncontentCanvas,
            Collectiontitle,
            Collectionalert
        },
        data() {
            return {
                clearnum: false,
                Erroralert: {
                    active: false
                },
            }
        },
        beforeRouteEnter(to, from, next) {
            if (window.localStorage.CollectionQuestion === '') {
                next('/collection/collectioncontentno')
            }
            next()
        },
        methods: {
            clearCollection() {
                this.Erroralert.active = true
            },
            Erroralerttogglefn(von, clearx) {
                this.Erroralert.active = von
                this.clearnum = true
                this.$router.push('collectioncontentno')
            }
        },
    }
</script>
<style lang="less">
    .collectioncontent {
        height: 100%;
        background: white;
        .collectioncontent-header {
            color: white;
            background: linear-gradient(rgb(20, 238, 209), rgb(12, 233, 141));
        }

        .collectioncontent-btn {
            position: fixed;
            bottom: 0;
            width: 100%;

            button {
                width: 100%;
                padding: 1rem 0;
                background: rgb(21, 202, 21);
                letter-spacing: 0.3rem;
                color: white;
                border: none;
            }
        }
    }
</style>